.nav {
  position: fixed;
  width: 100%;
  max-width: 16rem;
  top: 0;
  bottom: 0;
  left: 0;
  padding-top: 3.8rem;
  height: 100vh;
  z-index: 5;
  box-shadow: 8px 0 6px -6px rgba(0, 0, 0, 0.3);
  background-color: #f2f2f2;
  transform: translateX(-32rem);
  transition: transform .3s;
}

@media (min-width: 30em) {
  .nav {
    background-color: white;
    box-shadow: -1px 8px 6px -6px rgba(0, 0, 0, 0.3);
    height: auto;
    padding-left: 0;
    padding-top: 1rem;
    padding-bottom: 1rem;
    transform: translateX(0);
    max-width: initial;
    top: 0;
    bottom: auto;
    left: auto;
    right: auto;
  }
}

.nav ul {
  display: flex;
  flex-direction: column;
}

@media (min-width: 30em) {
  .nav ul {
    flex-direction: row;
    justify-content: flex-end;
  }
}

.nav.is-shown {
  transform: translateX(0);
}

.nav a {
  color: #9C27B0;
  text-decoration: none;
  cursor: pointer;
  display: block;
  transition: color .3s;
}

.nav a:hover {
  color: white;
}

.nav-link {
  padding-top: 1rem;
  padding-bottom: 1rem;
  cursor: pointer;
  border-top: 1px solid #cbcbcb;
  text-align: center;
  font-size: 1.2rem;
}

@media (min-width: 30em) {
  .nav-link {
    text-align: left;
    font-size: 1rem;
    border-top: 0;
    padding-top: 0;
    padding-bottom: 0;
    padding-right: 2rem;
  }
}

.nav-toggle {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  padding: 1rem 0 2.2rem 1rem;
  background-color: #f2f2f2;
  width: 100%;
  z-index: 10;
  cursor: pointer;
  box-shadow: -1px 8px 6px -6px rgba(0, 0, 0, 0.3);
  transition: box-shadow .3s;
}

@media (min-width: 30em) {
  .nav-toggle {
    display: none;
  }
}

.nav-toggle .toggle {
  position: relative;
  width: 3rem;
  margin: 0;
  padding: 0;
}

.nav-toggle .toggle span {
  display: block;
  width: 100%;
  height: .2rem;
  background-color: #9C27B0;
  transition: transform .3s;
  border-radius: 2px;
}

.nav-toggle .toggle:after, .nav-toggle .toggle:before {
  content: '';
  display: block;
  width: 100%;
  height: .2rem;
  position: absolute;
  bottom: -.55rem;
  background-color: #9C27B0;
  opacity: 1;
  transition: opacity .3s, transform .3s;
  border-radius: 2px;
}

.nav-toggle .toggle:after {
  bottom: -1.1rem;
}

.nav-toggle.is-shown {
  box-shadow: -1px 8px 6px -6px transparent;
}

.nav-toggle.is-shown .toggle span {
  transform: rotate(45deg) translate(0.4rem, 0.3rem);
}

.nav-toggle.is-shown .toggle:before {
  transform: rotate(-45deg);
}

.nav-toggle.is-shown .toggle:after {
  opacity: 0;
}

